function fun2() {
	let city = localStorage.getItem('cityName');
	//alert(city)
	if (city == "" || city == null) {
		getWeather('Plano');
		localStorage.setItem('cityName', 'Plano');
	}
	else
		getWeather(city);
}
fun2();
